





































import Vue from "vue";
import HelloWorld from "@/components/HelloWorld.vue"; // @ is an alias to /src
import utils from "@/assets/js/utils";

const rp = require("request-promise");
const apiRoot = utils.setApiUrl().url;

export default Vue.extend({
  name: "home",
  components: {
    HelloWorld
  },
  data() {
    const datats: any = {
      formAddExist: {
        exit: "",
        exitsArray: []
      },
      examples: [60 * 1000000, 25 * 1000000, 35 * 1000000, 45 * 1000000],
      challengeExits: [
        25 * 1000000,
        40 * 1000000,
        50 * 1000000,
        70 * 1000000,
        39 * 1000000,
        47 * 1000000
      ],
      exits: {}
    };
    return datats;
  },
  created() {
    this.getChallengeExits();
  },
  methods: {
    addExit() {
      this.formAddExist.exitsArray.push(this.formAddExist.exit * 1000000);
    },
    async getData(exits: Array<number>) {
      const url = apiRoot + "/api/share_holders";
      const args = {
        method: "POST",
        url,
        json: true,
        body: { exits }
      };
      this.exits = await rp(args);
    },
    getExamples() {
      this.getData(this.examples);
    },
    getChallengeExits() {
      this.getData(this.challengeExits);
    }
  }
});
