const utils = {
    env: null,
    firstLetterUppercase: (label: string) => {
        return label.charAt(0).toUpperCase() + label.substr(1);
    },
    setApiUrl: () => {

        let env, url, url2;
        switch (window.location.hostname) {
            default:
                url = window.location.protocol + "//" + window.location.hostname;
                console.log("default");

        }
        env = "local";
        if (env && window.location.hostname == "localhost") {
            switch (env) {
                case "local":
                    url = "http://localhost:3000";
                    break;
            }
        }
        return { url, url2 };
    },
    dataTime: (unix_timestamp: number) => {
        var date = new Date(unix_timestamp);

        var day = date.getDate();
        var month = date.getMonth() + 1;
        var year = date.getFullYear();
        var hours = date.getHours();
        var minutes = "0" + date.getMinutes();
        var seconds = "0" + date.getSeconds();

        var formattedTime =
            day +
            "/" +
            month +
            "/" +
            year +
            " " +
            hours +
            ":" +
            minutes.substr(-2) +
            ":" +
            seconds.substr(-2);

        return formattedTime;
    },
    secondsToHms: (d: number) => {
        d = Number(d);
        var h = Math.floor(d / 3600);
        var m = Math.floor(d % 3600 / 60);
        var s = Math.floor(d % 3600 % 60);

        var hDisplay = h > 0 ? h + (h == 1 ? " h " : " h ") : "";
        var mDisplay = m > 0 ? m + (m == 1 ? " m " : " m ") : "";
        var sDisplay = s > 0 ? s + (s == 1 ? " s" : " s") : "";
        return hDisplay + mDisplay + sDisplay;
    },
    sort: (array: [], key: string) => {
        var sortedArray = array.sort(
            (a, b) => parseFloat(b[key]) - parseFloat(a[key])
        );
        return sortedArray;
    },

};

export default utils;
